@use '../../shared/scss/variables.scss' as variables;

.header {
  background-color: variables.$primary;
}

.overlay {
  background-color: rgba(0, 0, 0, 0.5);
}

.custom-border {
  border-bottom: 1px solid variables.$background;
}

.logo {
  border: 1px solid variables.$background;

  &__text {
    font-family: 'Bungee', sans-serif;
    color: variables.$background;
    border-top: 1px solid variables.$background;
    border-bottom: 1px solid variables.$background;
  }
}

.active {
  color: variables.$background;
}
